.Dark {
  background-color: var(--darkBlue);
  color: var(--white);
}
.Light {
  background-color: var(--veryLightGray);
  color: var(--veryDarkBlue);
}
.Country {
  width: 280px;
  box-shadow: #64646f33 0px 7px 29px 0px;
  border-radius: 8px;
  margin-bottom: 25px;
}
.Country a {
  text-decoration: none;
  color: inherit;
}
.ImgDiv img {
  width: 100%;
  border-radius: 8px 8px 0 0;
}
.Description {
  padding: 16px;
}
.CountryName {
  margin-bottom: 16px;
  font-weight: 800;
}
.info {
  font-weight: 600;
}
